<template>
  <div>
    <form @submit.prevent="guardar">
      <!-- Campos del Resumen Ejecutivo -->
      <h3>Resumen Ejecutivo</h3>
      <div class="form-group">
        <label>Mes:</label>
        <input v-model="localMes" type="text" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Año:</label>
        <input v-model="localAño" type="text" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Total de Vulnerabilidades:</label>
        <input v-model="localTotalVulnerabilidades" type="number" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Vulnerabilidades Críticas:</label>
        <input v-model="localVulnerabilidadesCriticas" type="number" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Vulnerabilidades Altas:</label>
        <input v-model="localVulnerabilidadesAltas" type="number" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Vulnerabilidades Medias:</label>
        <input v-model="localVulnerabilidadesMedias" type="number" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Sectores Afectados:</label>
        <input v-model="localSectoresAfectados" type="text" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Grupos de Ransomware:</label>
        <input v-model="localGruposRansomware" type="text" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Campañas de Ingeniería Social:</label>
        <input v-model="localCampañasIngenieria" type="text" class="form-control" required />
      </div>

      <!-- Indicadores del Dashboard -->
      <h3>Indicadores del Dashboard</h3>
      <div class="form-group">
        <label>Incidentes (Valor):</label>
        <input v-model="localIndicadores.incidentes.valor" type="number" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Incidentes (Porcentaje):</label>
        <input v-model="localIndicadores.incidentes.porcentaje" type="text" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Incidentes (Clase):</label>
        <select v-model="localIndicadores.incidentes.clase" class="form-control" required>
          <option value="text-danger">Alto (text-danger)</option>
          <option value="text-success">Bajo (text-success)</option>
          <option value="text-muted">Neutral (text-muted)</option>
        </select>
      </div>

      <div class="form-group">
        <label>Zero Days (Valor):</label>
        <input v-model="localIndicadores.zeroDays.valor" type="number" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Zero Days (Porcentaje):</label>
        <input v-model="localIndicadores.zeroDays.porcentaje" type="text" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Zero Days (Clase):</label>
        <select v-model="localIndicadores.zeroDays.clase" class="form-control" required>
          <option value="text-danger">Alto (text-danger)</option>
          <option value="text-success">Bajo (text-success)</option>
          <option value="text-muted">Neutral (text-muted)</option>
        </select>
      </div>

      <div class="form-group">
        <label>Exploits (Valor):</label>
        <input v-model="localIndicadores.exploits.valor" type="number" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Exploits (Porcentaje):</label>
        <input v-model="localIndicadores.exploits.porcentaje" type="text" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Exploits (Clase):</label>
        <select v-model="localIndicadores.exploits.clase" class="form-control" required>
          <option value="text-danger">Alto (text-danger)</option>
          <option value="text-success">Bajo (text-success)</option>
          <option value="text-muted">Neutral (text-muted)</option>
        </select>
      </div>

      <div class="form-group">
        <label>Nuevos CVE (Valor):</label>
        <input v-model="localIndicadores.nuevosCVE.valor" type="number" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Nuevos CVE (Porcentaje):</label>
        <input v-model="localIndicadores.nuevosCVE.porcentaje" type="text" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Nuevos CVE (Clase):</label>
        <select v-model="localIndicadores.nuevosCVE.clase" class="form-control" required>
          <option value="text-danger">Alto (text-danger)</option>
          <option value="text-success">Bajo (text-success)</option>
          <option value="text-muted">Neutral (text-muted)</option>
        </select>
      </div>

      <div class="form-group">
        <label>Mitigaciones (Valor):</label>
        <input v-model="localIndicadores.mitigaciones.valor" type="number" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Mitigaciones (Porcentaje):</label>
        <input v-model="localIndicadores.mitigaciones.porcentaje" type="text" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Mitigaciones (Clase):</label>
        <select v-model="localIndicadores.mitigaciones.clase" class="form-control" required>
          <option value="text-danger">Alto (text-danger)</option>
          <option value="text-success">Bajo (text-success)</option>
          <option value="text-muted">Neutral (text-muted)</option>
        </select>
      </div>

      <div class="form-group">
        <label>Alertas (Valor):</label>
        <input v-model="localIndicadores.alertas.valor" type="number" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Alertas (Porcentaje):</label>
        <input v-model="localIndicadores.alertas.porcentaje" type="text" class="form-control" required />
      </div>
      <div class="form-group">
        <label>Alertas (Clase):</label>
        <select v-model="localIndicadores.alertas.clase" class="form-control" required>
          <option value="text-danger">Alto (text-danger)</option>
          <option value="text-success">Bajo (text-success)</option>
          <option value="text-muted">Neutral (text-muted)</option>
        </select>
      </div>

      <!-- Incidentes Detectados -->
      <h3>Incidentes Detectados</h3>
      <div v-for="(incidente, index) in localIncidentes" :key="index" class="form-group">
        <label>Fecha:</label>
        <input v-model="incidente.fecha" type="text" class="form-control" required />
        <label>Empresa:</label>
        <input v-model="incidente.empresa" type="text" class="form-control" required />
        <label>Sector:</label>
        <input v-model="incidente.sector" type="text" class="form-control" required />
        <label>País:</label>
        <input v-model="incidente.pais" type="text" class="form-control" required />
        <label>Descripción:</label>
        <input v-model="incidente.descripcion" type="text" class="form-control" required />
        <button type="button" class="btn btn-danger btn-sm mt-2" @click="eliminarIncidente(index)">
          Eliminar
        </button>
      </div>
      <button type="button" class="btn btn-secondary mb-4" @click="agregarIncidente">
        Agregar Incidente
      </button>

      <!-- Exploits Publicados -->
      <h3>Exploits Publicados</h3>
      <div v-for="(exploit, index) in localExploits" :key="index" class="form-group">
        <label>Fecha:</label>
        <input v-model="exploit.fecha" type="text" class="form-control" required />
        <label>Nombre:</label>
        <input v-model="exploit.nombre" type="text" class="form-control" required />
        <label>Tipo:</label>
        <input v-model="exploit.tipo" type="text" class="form-control" required />
        <label>Plataforma:</label>
        <input v-model="exploit.plataforma" type="text" class="form-control" required />
        <label>Descripción:</label>
        <input v-model="exploit.descripcion" type="text" class="form-control" required />
        <button type="button" class="btn btn-danger btn-sm mt-2" @click="eliminarExploit(index)">
          Eliminar
        </button>
      </div>
      <button type="button" class="btn btn-secondary mb-4" @click="agregarExploit">
        Agregar Exploit
      </button>

      <!-- Vulnerabilidades CVE -->
      <h3>Vulnerabilidades CVE</h3>
      <div v-for="(cve, index) in localCves" :key="index" class="form-group">
        <label>ID:</label>
        <input v-model="cve.id" type="text" class="form-control" required />
        <label>Aplicativo Afectado:</label>
        <input v-model="cve.aplicativo" type="text" class="form-control" required />
        <label>Fecha de Publicación:</label>
        <input v-model="cve.fecha" type="text" class="form-control" required />
        <label>CVSS:</label>
        <input v-model="cve.cvss" type="text" class="form-control" required />
        <label>Descripción:</label>
        <input v-model="cve.descripcion" type="text" class="form-control" required />
        <button type="button" class="btn btn-danger btn-sm mt-2" @click="eliminarCve(index)">
          Eliminar
        </button>
      </div>
      <button type="button" class="btn btn-secondary mb-4" @click="agregarCve">
        Agregar CVE
      </button>

      <!-- Alertas de Amenazas -->
      <h3>Alertas de Amenazas</h3>
      <div v-for="(alerta, index) in localAlertas" :key="index" class="form-group">
        <label>Tipo:</label>
        <input v-model="alerta.tipo" type="text" class="form-control" required />
        <label>Descripción:</label>
        <input v-model="alerta.descripcion" type="text" class="form-control" required />
        <label>Severidad:</label>
        <input v-model="alerta.severidad" type="text" class="form-control" required />
        <button type="button" class="btn btn-danger btn-sm mt-2" @click="eliminarAlerta(index)">
          Eliminar
        </button>
      </div>
      <button type="button" class="btn btn-secondary mb-4" @click="agregarAlerta">
        Agregar Alerta
      </button>

      <!-- Fuentes de Inteligencia -->
      <h3>Fuentes de Inteligencia</h3>
      <div v-for="(fuente, index) in localFuentes" :key="index" class="form-group">
        <label>Nombre:</label>
        <input v-model="fuente.nombre" type="text" class="form-control" required />
        <label>Referencia:</label>
        <input v-model="fuente.referencia" type="text" class="form-control" required />
        <button type="button" class="btn btn-danger btn-sm mt-2" @click="eliminarFuente(index)">
          Eliminar
        </button>
      </div>
      <button type="button" class="btn btn-secondary mb-4" @click="agregarFuente">
        Agregar Fuente
      </button>

      <!-- Mitigaciones -->
      <h3>Mitigaciones</h3>
      <div v-for="(mitigacion, index) in localMitigaciones" :key="index" class="form-group">
        <label>Mitigación:</label>
        <input v-model="localMitigaciones[index]" type="text" class="form-control" required />
        <button type="button" class="btn btn-danger btn-sm mt-2" @click="eliminarMitigacion(index)">
          Eliminar
        </button>
      </div>
      <button type="button" class="btn btn-secondary mb-4" @click="agregarMitigacion">
        Agregar Mitigación
      </button>

      <!-- Botones -->
      <div class="form-group d-flex justify-content-end">
        <button type="submit" class="btn btn-primary mr-2">Guardar</button>
        <button type="button" class="btn btn-secondary" @click="cerrar">Cerrar</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  props: {
    mes: String,
    año: String,
    totalVulnerabilidades: Number,
    vulnerabilidadesCriticas: Number,
    vulnerabilidadesAltas: Number,
    vulnerabilidadesMedias: Number,
    sectoresAfectados: String,
    gruposRansomware: String,
    campañasIngenieria: String,
    indicadores: Object,
    incidentes: Array,
    exploits: Array,
    cves: Array,
    alertas: Array,
    fuentes: Array,
    mitigaciones: Array,
  },
  data() {
    return {
      localMes: this.mes,
      localAño: this.año,
      localTotalVulnerabilidades: this.totalVulnerabilidades,
      localVulnerabilidadesCriticas: this.vulnerabilidadesCriticas,
      localVulnerabilidadesAltas: this.vulnerabilidadesAltas,
      localVulnerabilidadesMedias: this.vulnerabilidadesMedias,
      localSectoresAfectados: this.sectoresAfectados,
      localGruposRansomware: this.gruposRansomware,
      localCampañasIngenieria: this.campañasIngenieria,
      localIndicadores: { ...this.indicadores },
      localIncidentes: [...this.incidentes],
      localExploits: [...this.exploits],
      localCves: this.cves.map(cve => ({
        ...cve,
        showDescription: false,
      })),
      localAlertas: [...this.alertas],
      localFuentes: [...this.fuentes],
      localMitigaciones: [...this.mitigaciones],
    };
  },
  methods: {
    agregarIncidente() {
      this.localIncidentes.push({
        fecha: '',
        empresa: '',
        sector: '',
        pais: '',
        descripcion: '',
      });
    },
    eliminarIncidente(index) {
      this.localIncidentes.splice(index, 1);
    },
    agregarExploit() {
      this.localExploits.push({
        fecha: '',
        nombre: '',
        tipo: '',
        plataforma: '',
        descripcion: '',
      });
    },
    eliminarExploit(index) {
      this.localExploits.splice(index, 1);
    },
    agregarCve() {
      this.localCves.push({
        id: '',
        aplicativo: '',
        fecha: '',
        cvss: '',
        descripcion: '',
      });
    },
    eliminarCve(index) {
      this.localCves.splice(index, 1);
    },
    agregarAlerta() {
      this.localAlertas.push({
        tipo: '',
        descripcion: '',
        severidad: '',
      });
    },
    eliminarAlerta(index) {
      this.localAlertas.splice(index, 1);
    },
    agregarFuente() {
      this.localFuentes.push({
        nombre: '',
        referencia: '',
      });
    },
    eliminarFuente(index) {
      this.localFuentes.splice(index, 1);
    },
    agregarMitigacion() {
      this.localMitigaciones.push('');
    },
    eliminarMitigacion(index) {
      this.localMitigaciones.splice(index, 1);
    },
    guardar() {
      const nuevosDatos = {
        mes: this.localMes,
        año: this.localAño,
        totalVulnerabilidades: this.localTotalVulnerabilidades,
        vulnerabilidadesCriticas: this.localVulnerabilidadesCriticas,
        vulnerabilidadesAltas: this.localVulnerabilidadesAltas,
        vulnerabilidadesMedias: this.localVulnerabilidadesMedias,
        sectoresAfectados: this.localSectoresAfectados,
        gruposRansomware: this.localGruposRansomware,
        campañasIngenieria: this.localCampañasIngenieria,
        indicadores: this.localIndicadores,
        incidentes: this.localIncidentes,
        exploits: this.localExploits,
        cves: this.localCves,
        alertas: this.localAlertas,
        fuentes: this.localFuentes,
        mitigaciones: this.localMitigaciones,
      };
      this.$emit('guardar', nuevosDatos);
    },
    cerrar() {
      this.$emit('cerrar');
    },
  },
};
</script>

<style scoped>
.form-group {
  margin-bottom: 1rem;
}

.form-control {
  width: 100%;
  padding: 0.5rem;
  border-radius: 4px;
  border: 1px solid #ccc;
}

select.form-control {
  appearance: none;
  background-color: white;
  cursor: pointer;
}

.btn-danger {
  background-color: #dc3545;
  color: white;
}

.btn-secondary {
  background-color: #6c757d;
  color: white;
}

.d-flex {
  display: flex;
}

.justify-content-end {
  justify-content: flex-end;
}

.mr-2 {
  margin-right: 0.5rem;
}
</style>